  const [dailyReportStats, setDailyReportStats] = useState<{ count: number; sales: number; cost: number; profit: number } | null>(null);
  const [isFetchingDailyReport, setIsFetchingDailyReport] = useState<boolean>(false);

  const fetchDailyReport = async (dateStr: string) => {
    if (!user?.uid || !dateStr) return;
    setIsFetchingDailyReport(true);
    setDailyReportStats(null);
    try {
      const salesRef = collection(db, 'users', user.uid, 'dailySales');
      const q = query(salesRef, where('date', '==', dateStr));
      const snapshot = await getDocs(q);
      
      let count = 0;
      let sales = 0;
      let cost = 0;
      let profit = 0;
      
      snapshot.forEach(doc => {
        const data = doc.data();
        const qty = Number(data.quantity || 1);
        count++; // Count transactions, not quantity
        sales += Number(data.sellingPrice || 0) * qty;
        cost += Number(data.wholesalePrice || 0) * qty;
        profit += Number(data.profit || 0);
      });
      
      setDailyReportStats({ count, sales, cost, profit });
    } catch (e) {
      console.error(e);
      toast({ title: 'خطأ', description: 'فشل جلب التقارير', variant: 'destructive' });
    } finally {
      setIsFetchingDailyReport(false);
    }
  };

  const [salesData, setSalesData] = useState<SaleItem[]>([]);
  const [filterStartDate, setFilterStartDate] = useState('');
  const [filterEndDate, setFilterEndDate] = useState('');

  const handleDateFilter = async () => {
    if (!user?.uid) return;
    if (!filterStartDate && !filterEndDate) {
      toast({ title: 'تنبيه', description: 'يرجى اختيار تاريخ للبحث', variant: 'destructive' });
      return;
    }

    try {
      const sid = shopIdCtx || (profile as any)?.shopId || null;
      
      // Query 2: Sub-collection (Always tries this first as it's more reliable for per-user data and allows date filtering without composite index on userId+date)
      // Note: date filter inside users/{uid}/dailySales only needs index on 'date' which is automatic.
      const q2 = query(
        collection(db, 'users', user.uid, 'dailySales'),
        ...(sid ? [where('shopId', '==', sid)] : []),
        ...(filterStartDate ? [where('date', '>=', filterStartDate)] : []),
        ...(filterEndDate ? [where('date', '<=', filterEndDate)] : [])
      );

      // Query 1: Root collection (Might fail if missing composite index)
      let q1 = query(
        collection(db, 'dailySales'), 
        where('userId', '==', user.uid),
        ...(sid ? [where('shopId', '==', sid)] : [])
      );
      if (filterStartDate) q1 = query(q1, where('date', '>=', filterStartDate));
      if (filterEndDate) q1 = query(q1, where('date', '<=', filterEndDate));

      // Execute queries separately to handle failures gracefully
      let docs1: any[] = [];
      let docs2: any[] = [];

      try {
        const snap2 = await getDocs(q2);
        docs2 = snap2.docs;
      } catch (e) {
        console.warn('Sub-collection query failed', e);
      }

      try {
        const snap1 = await getDocs(q1);
        docs1 = snap1.docs;
      } catch (e) {
        console.warn('Root collection indexed query failed (likely missing index). Falling back to basic fetch.', e);
        // Fallback: Fetch recent sales by userId only, then filter in memory
        try {
          const fallbackQ = query(
             collection(db, 'dailySales'),
             where('userId', '==', user.uid),
             ...(sid ? [where('shopId', '==', sid)] : []),
             orderBy('date', 'desc'),
             limit(1000) // Safety limit
          );
           // Try with sort first (might also need index if filtering by userId)
           // Actually: where(userId) + orderBy(date) NEEDS index.
           // So just where(userId) + limit(2000). No sort? No, random results.
           // To be safe: just where(userId) + limit(2000)
           const safeFallback = query(
             collection(db, 'dailySales'),
             where('userId', '==', user.uid),
             ...(sid ? [where('shopId', '==', sid)] : []),
             limit(2000)
           );
           const fallbackSnap = await getDocs(safeFallback);
           // Filter in memory
           docs1 = fallbackSnap.docs.filter(d => {
              const data = d.data();
              if (filterStartDate && data.date < filterStartDate) return false;
              if (filterEndDate && data.date > filterEndDate) return false;
              return true;
           });
        } catch (e2) {
           console.error('Fallback query also failed', e2);
        }
      }

      const allDocs = [...docs1, ...docs2];
      
      if (allDocs.length === 0) {
        setSalesData([]);
        toast({ title: 'بحث', description: 'لم يتم العثور على نتائج' });
        return;
      }

      // Deduplicate by ID
      const uniqueDocs = new Map();
      allDocs.forEach(d => {
        const data = d.data();
        const id = data.clientId || d.id;
        if (!uniqueDocs.has(id)) {
           uniqueDocs.set(id, { ...data, firestoreId: d.id });
        }
      });

      const filteredSales = Array.from(uniqueDocs.values()).map(data => {
        return {
           id: data.clientId || data.firestoreId,
           productName: data.productName,
           price: Number(data.sellingPrice || 0),
           wholesalePrice: Number(data.wholesalePrice || 0),
           quantity: Number(data.quantity || 0),
           date: data.date,
           time: data.time,
           serialNumber: data.serialNumber,
           barcode: data.barcode,
           performedByType: data.performedByType,
           performedByName: data.performedByName,
           performedByUsername: data.performedByUsername,
           isDeferred: !!data.isDeferred,
           customerName: data.customerName,
           customerMobile: data.customerMobile,
           customerId: data.customerId,
           firestoreId: data.firestoreId,
           cartItems: data.cartItems,
           createdAtMs: data.createdAt?.toDate?.() ? data.createdAt.toDate().getTime() : 0,
           transactionReference: data.reference // if exists
        } as SaleItem;
      });

      // Sort in memory (Date desc, Time desc)
      filteredSales.sort((a, b) => {
          if (a.date !== b.date) return b.date.localeCompare(a.date);
          return (b.time || '').localeCompare(a.time || '');
      });

      setSalesData(filteredSales);
      toast({ title: 'تم البحث', description: `تم العثور على ${filteredSales.length} عملية بيع` });

    } catch (e) {
      console.error('Search error:', e);
      toast({ title: 'خطأ', description: 'حدث خطأ أثناء البحث', variant: 'destructive' });
    }
  };

