// تحميل JsBarcode من CDN عند الحاجة
const loadJsBarcode = async () => {
  if ((window as any).JsBarcode) return;
  await new Promise<void>((resolve, reject) => {
    const s = document.createElement('script');
    s.src = 'https://cdn.jsdelivr.net/npm/jsbarcode@3.11.5/dist/JsBarcode.all.min.js';
    s.onload = () => resolve();
    s.onerror = () => reject(new Error('Failed to load JsBarcode'));
    document.head.appendChild(s);
  });
};

const fetchBwipBase64 = async (code: string) => {
  const url = `https://bwipjs-api.metafloor.com/?bcid=code128&text=${encodeURIComponent(code)}&scale=3&height=10&includetext=1`;
  const resp = await fetch(url, { cache: 'no-store' });
  const blob = await resp.blob();
  return await new Promise<string>((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => resolve((reader.result as string) || '');
    reader.onerror = (err) => reject(err);
    reader.readAsDataURL(blob);
  });
};

useEffect(() => {
  const code = (newProduct.barcode || '').trim();
  if (!code) { setBarcodeBase64(''); setBarcodePreviewUrl(''); return; }
  setIsBarcodeLoading(true);
  (async () => {
    try {
      await loadJsBarcode();
      if (!(window as any).JsBarcode) throw new Error('JsBarcode unavailable');
      const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
      (window as any).JsBarcode(svg, code, { format: 'CODE128', displayValue: true, fontSize: 12, height: 70, margin: 0 });
      const xml = new XMLSerializer().serializeToString(svg);
      const base64 = 'data:image/svg+xml;base64,' + btoa(unescape(encodeURIComponent(xml)));
      setBarcodeBase64(base64);
      setBarcodePreviewUrl('');
    } catch (e) {
      console.warn('JsBarcode failed, using bwip fallback', e);
      try {
        const base64 = await fetchBwipBase64(code);
        setBarcodeBase64(base64);
        setBarcodePreviewUrl('');
      } catch (e2) {
        console.error('barcode fallback error', e2);
        setBarcodeBase64('');
      }
    } finally {
      setIsBarcodeLoading(false);
    }
  })();
}, [newProduct.barcode]);

const escapeHtml = (s: string) => (s || '').replace(/[&<>]/g, (c) => ({'&':'&amp;','<':'&lt;','>':'&gt;'}[c] as string));

const printBarcodeLabel = () => {
  const code = (newProduct.barcode || '').trim();
  if (!code || !(barcodeBase64 || barcodePreviewUrl)) {
    toast({ title: 'الباركود مطلوب', description: 'أنشئ باركود أو أدخله قبل الطباعة', variant: 'destructive' });
    return;
  }
  try {
    const src = barcodeBase64 || barcodePreviewUrl;
    const w = window.open('', 'PRINT', 'width=600,height=400');
    w?.document.write(`
      <html dir="rtl">
        <head>
          <meta charset="utf-8" />
          <title>طباعة باركود</title>
          <style>
            @page { size: 50mm 30mm; margin: 4mm; }
            body { margin: 0; padding: 0; font-family: system-ui, sans-serif; }
            .label { width: 100%; height: 100%; display:flex; flex-direction:column; align-items:center; justify-content:center; }
            .name { font-size: 12px; margin-bottom: 6px; }
            img { width: 100%; height: auto; }
          </style>
        </head>
        <body>
          <div class="label">
            <div class="name">${escapeHtml(newProduct.productName || '')}</div>
            <img src="${src}" alt="barcode" />
          </div>
          <script>setTimeout(() => window.print(), 150);</script>
        </body>
      </html>
    `);
    w?.document.close();
  } catch (e) {
    console.error(e);
    toast({ title: 'فشل الطباعة', description: 'تعذّر فتح نافذة الطباعة', variant: 'destructive' });
  }
};
const [isScannerOpen, setIsScannerOpen] = useState<boolean>(false);
