const html5QrRef = React.useRef<any>(null);
const scannerContainerId = 'barcode-scanner-container';

const startScan = async () => {
  try {
    const formats = Html5QrcodeSupportedFormats;
    const devices = await Html5Qrcode.getCameras();
    if (!devices || devices.length === 0) throw new Error('لا توجد كاميرات متاحة');
    const back = devices.find((d: any) => /back|rear|خلف/i.test(d.label)) || devices[devices.length - 1];
    const deviceId = back.id;
    html5QrRef.current = new Html5Qrcode(scannerContainerId);
    await html5QrRef.current.start(
      deviceId,
      { fps: 10, qrbox: { width: 250, height: 120 }, formatsToSupport: [formats.CODE_128, formats.EAN_13, formats.EAN_8, formats.CODE_39] },
      (decodedText: string) => {
        setNewProduct(p => ({ ...p, barcode: decodedText }));
        stopScan();
      },
      () => {}
    );
  } catch (e: any) {
    console.error('scanner start error', e);
    setScannerError(e?.message || 'تعذّر بدء الكاميرا');
  }
};

const stopScan = async () => {
  try {
    if (html5QrRef.current) {
      await html5QrRef.current.stop();
      await html5QrRef.current.clear();
      html5QrRef.current = null;
    }
  } catch (e) {
    console.warn('scanner stop warn', e);
  }
  setIsScannerOpen(false);
};

